:root {
    --bs-border-color: var(--tc-system-stroke) !important;
    --bs-dark-rgb: 41, 41, 41;
    --bs-primary-rgb: 247, 119, 94;

    .bg-secondary-2 {
        background-color: #ebeff4;
    }

    .btn {
        border-radius: 0;
    }

    .text-secondary {
        color: var(--tc-body-text) !important;
    }
}

// text colors

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

.stroke-primary {
    path {
        stroke: var(--tc-theme-primary) !important;
    }

    circle {
        stroke: var(--tc-theme-primary) !important;
    }
}

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
